  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
nav,
hgroup,
menu,
nav,
section {
  display: block;
}
p {
  margin-bottom: 0.15rem;
}
/*设置列表标签通用样式*/
ul,
ol {
  list-style: none;
  list-style-image: none;
}
ul li,
ol li {
  list-style: none;
}
/*设置超链接标签通用样式*/
a {
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
  background: transparent;
  vertical-align: baseline;
  color: #333;
}
a:hover {
  color: #3B89C0;
}
a.disabled {
  cursor: default;
}
/*设置超链接标签通用样式*/
img {
  border: 0;
  height: auto;
  width: 100%;
  -ms-interpolation-mode: bicubic;
}
/* 设置输入框通用样式 */
input[type="text"],
input[type="password"],
textarea {
  border: 1px solid #ccc;
  outline: none;
  width: 100%;
  margin-bottom: 0.15rem;
  font-size: 0.14rem;
  font-family: "microsoft yahei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  border-radius: 4px;
}
input[type="text"],
input[type="password"] {
  text-indent: 10px;
}
textarea {
  padding: 0.1rem;
}
button,
input {
  display: block;
  outline: none;
  font-size: 0.14rem;
  height: 0.35rem;
  line-height: 0.35rem;
  font-family: "microsoft yahei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  border-radius: 4px;
}
/* 设置按钮通用样式 */
button,
input[type="button"],
input[type="submit"] {
  border: none;
  background: #3B89C0;
  color: #fff;
  text-align: center;
  cursor: pointer;
}
/* 设置浮动类型 */
.fl {
  float: left;
}
.fr {
  float: right;
}
/* 设置清除浮动类型 */
.clearfix:after {
  clear: both;
}
.clearfix:before,
.clearfix:after {
  content: '.';
  display: block;
  overflow: hidden;
  visibility: hidden;
  font-size: 0;
  line-height: 0;
  width: 0;
  height: 0;
}
/* page setting */
.fullwidth {
  width: 100%;
}
.edge_frame {
  padding-left: 0.08rem;
  padding-right: 0.08rem;
}
.content {
  margin-top: 0.4rem;
  margin-bottom: 0.43rem;
}
/* wrapper */
.wrapper {
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;
  background: #fff;
  position: relative;
  overflow-x: hidden;
}
/* push_body */
.push_body {
  overflow-x: hidden;
  position: relative;
  left: 0;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.push_body_toright {
  left: 2.4rem;
}
/* icon-list-next */
.icon-list-next {
  display: block;
  float: right;
  margin-top: HalfRem(26px);
  background-size: 100%;
  width: 0.09rem;
  height: 0.15rem;
}
/* layout */
@media screen and (min-width: 375px) {
  /* solution */
.solution-list .solution-desc {
    width: 68%;
}
}
@media screen and (min-width: 414px) {
html {
    font-size: 690%;
}
  /* solution */
.solution-list p {
    font-size: 0.14rem;
}
}
@media screen and (min-width: 480px) {
html {
    font-size: 800%;
}
}
@media screen and (min-width: 640px) {
html {
    font-size: 938%;
}
}
@media screen and (min-width: 768px) {
  /* solution */
.solution-list .solution-desc {
    width: 70%;
}
}
@media screen and (min-width: 960px) {
body {
    background: #eee;
}
.wrapper {
    width: 640px;
}
.edge_frame {
    padding-left: 0.15rem;
    padding-right: 0.15rem;
}
.push_body_toright {
    left: 0;
}
}
.copyright {
  border-top: 1px solid #ccc;
  padding: 0.08rem;
  font-size: 0.12rem;
  margin-top: 0.15rem;
}
.copyright p {
  color: #888;
  text-align: center;
  margin-bottom: 0;
}

